.about {
  position: absolute;
  right: -110px;
  top: -50px;
}

.services__section{
  position: relative;
  background-image: var(--second-gradient);
  
  display:flex;
  overflow: hidden;
  flex-direction: column;
  height: 100vh;
  align-items: center;
  justify-content: center;
}
.section__title,
.section__subtitle {
  text-align: center;

}

.section__title{
  position: relative;
  top: -92px;
  font-size: var(--h1-font-size);
  text-align: center;
  font-size: 40px;
  
  
}
.section__title span {
  color: var(--primary-color);
}
.section__subtitle{
  color: var(--primary-color);
  font-size: var(--smaller-font-size);
  text-transform: uppercase;
  font-weight: 100;
}

.section__subtitle span{
  font-family: var(--second-font);
  color: var(--title-color);
  font-size: var(--h3-font-size);
  text-transform: capitalize;
  margin-left: 10px;
}
.section__cosubtitle {
  font-family: var(--second-font);
  color: var(--title-color);
  font-size: var(--h3-font-size);
  text-transform: capitalize;
  margin-left: 10px;

}

.section__coosubtitle {
  font-family: var(--second-font);
  color: var(--title-color);
  font-size: var(--h3-font-size);
  text-transform: capitalize;
  margin-left: 10px;
}

.services {
    background-image: var(--second-gradient);
}

.services__box{
  display: flex;
  height: fit-content;
  width: 90%;
  
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;

}
.services_img_outer{
  display: flex;
  position: relative;
  height: 350px;
  width: 350px;
  align-items: center;
  justify-content: center;
  
  
}

.border__rotate{
  position: absolute;
  display: block;
  border-radius: 50%;
  align-items: center;
  justify-content: center;
  border-top: 7px solid var(--primary-color);
  border-bottom:7px solid var(--primary-color); 
  height: 350px;
  width: 350px;
  animation: rotation 3s infinite linear;

}

@keyframes rotation {
  from {transform: rotate(0deg);}
  to {transform: rotate(360deg);}
}
.services__img{
  display: block;
  height: 300px;
  width: 300px;
  overflow: hidden;
  border-radius: 50%;
  position: absolute;
  background-color: aqua;
}

.my_photo{
  position: absolute;
  top: -50px;

}
.services__box1{
  display: flex;
  flex-direction: column;
  height: 400px;
  width: 400px;
  background-color: #1a1f26;
  border-radius: 25px;
  padding: 20px;
  align-items: flex-end;
  border: 4px solid var(--primary-color);

}
.services__box2{
  display: flex;
  flex-direction: column;
  height: 400px;
  width: 400px;
  background-color: #1a1f26;
  border-radius: 25px;
  padding: 20px;
  align-items: flex-end;
  border: 4px solid var(--primary-color);
}

.services__box1__container,.services__box2__container{
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
}

.services__box1__container span, .services__box2__container span {
  color: var(--primary-color);
  margin-bottom: 20px;
}

.services__box1__container h1, .services__box2__container h1 {
  
  margin-bottom: 10px;
}

.services__box1 button, .services__box2 button {
  width: 120px;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;

}
.pop__services1 {
  display: flex;
  border-radius: 30px;
  position: absolute;
  height: 500px;
  width: 40%;
  scale: 0;
  transition: 0.5s ease-in-out;
  border: 3px solid var(--primary-color);
  box-shadow: 0 0 8px var(--primary-color);
  backdrop-filter: blur(5px);
  background-color: rgba(4, 4, 5, 0.5);
}

.pop__services2 {
  display: flex;
  border-radius: 30px;
  position: absolute;
  height: 500px;
  width: 40%;
  scale: 0;
  transition: 0.5s ease-in-out;
  border: 3px solid var(--primary-color);
  box-shadow: 0 0 8px var(--primary-color);
  backdrop-filter: blur(5px);
  background-color: rgba(4, 4, 5, 0.5);
}


.active__servicespop1 {
  scale: 1;
}
.active__servicespop2 {
  scale: 1;

}

.pop__services1 i{
  position: relative;
  
  margin-left: auto;


}

.pop__services2 i{
  position: relative;
  
  margin-left: auto;


}
@media (max-width:922px){
  .services__section{
    height: fit-content;

  }
  .section__title{
    top: 0;
    margin-top: 20px;
    margin-bottom: 20px;

  }
  .services__box{
    
    width: 100%;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
  }
  .services_img_outer{
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .pop__services1{
    width: 90%;
  }
  .pop__services2{
    width: 90%;
  }
  
  
  
  
}