.home__outermost{
    position: relative;
    display: flex;
    width: 100%;
   
    align-items: center;
    justify-content: space-evenly;
    
    flex-wrap: wrap-reverse;
}

.home {
    position:relative;
    display: flex;
    background-color: var(--first-gradient);
    height: 100vh;
    align-items: center;
    overflow: hidden;
   
    
   
}

.home__container {
    display: flex;
    margin-left: 55px;
    margin-top: 50px;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    height: fit-content;
    width: fit-content;
    
    
}

.home__subtitle,.home__job {
    font-weight: 900;
}

.home__subtitle, .home__job span {
    font-size: 19px;
    color: smokewhite;
}

.home__subtitle span,
.home__title span{
    color: var(--primary-color);
}

.home__job  {
    font-family: var(--second-font);
    font-size: var(--h2-font-size);
    
  
}
.home__job span{
    font-size: 30px;
    color: aqua;

}

.home__title {
    font-size: 80px;
    text-shadow: 2px 2px hsl(0, 0%, 0%),-2px 2px hsl(0, 0%, 0%),2px -2px
    hsl(0, 0%, 0%),-2px -2px hsl(0, 0%, 0%), 5px 5px 0 rgb(0 0 0/20%);
    line-height: 1.2;
    margin-top: 25px;
}
.home__text {
    font-size: var(--large-font-size);
    margin-block: 10px 18px;
    max-width: 520px;
}


.home_socials {
    display: flex;
    margin-top: 10px;
    column-gap: 25px;
    margin-bottom: 15px;
}

.home_social-link {
    color: var(--title-color);
    font-size: var(--h5-font-size);
    transition: all 0.7s var(--transition);
}
 
.home_social-link:hover {
    color: var(--primary-color);
}

.home__btns {
    display: flex;
    column-gap: 70px;
    position: relative;
    margin-top: 25px;

}

.hero__link::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 58%;
    transform: translateY(-50%);
    width: 50px;
    height: 2px;
    background-color: var(--border-color);
    
  }

  .home__img--wrapper {
    position: relative;
    display: flex;
    top: 20px;

  }

   .home__banner {
    display: flex;
    background-color: var(--primary-color);
    height: 450px;
    width: 450px;
    /*padding-bottom: 100%;*/
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    z-index: 1;

  }

  .home__profile {
    background-blend-mode:darken;
    width: 95%;
    bottom: 0;
    display: flex;
    height: 700px;
   }
.home__data{
    position: absolute;
    display: flex;
    border: 2px solid var(--border-color);
    background-color: var(--comtainer-color);
    box-shadow: var(--shadow);
    padding-inline: 30px;
    width: 200px;
    height: 65px;
    align-items: center;
    justify-content: space-evenly;

    border-radius: 82px;
    z-index: 1;
}
.home__data .text-lg,
.home__data .text-sm{
    width: 70px;
    font-weight: 900;
    
    color:var(--title-color)
    
    
}
.home__data:hover{
    box-shadow: 0 0 20px var(--primary-color);
}
.home__data .text-lg b,
.home__data .text-sm span{
    color: var(--primary-color);
}
.home__data .text-lg{
    font-size: 30px;
    position: relative;
    left:-20px
  
    
}
.home__data .text-sm{
    font-size: 13px;
    position: relative;
    left: -20px;
}
.home__data-one{
    
    bottom:-9%;
    left: -15%;
}

.home__data-two{
    left:-100px;
    bottom:95%;
    
}

.liner{
    position: absolute;
    left: -240px;
    height: 650px;
    
    bottom: -120px;

    
}


.shape-1 {
    position: absolute;
    filter: invert(100%);
    width: 226px;
    height: 226px;
    top: -6%;
    right: 6%;
}

.shape-2 {
    position: absolute;
    filter: invert(100%);
    width: 141px;
    height: 141px;
    bottom: 8%;
    left: -16%;
}

.shape-3 {
    position: absolute;
    filter: invert(100%);
   
    bottom: 0%;
    right: -30px;

    width: 141px;
    height: 141px;
   
}

.shape-4 {
    position: absolute;
    filter: invert(100%);
    width: 141px;
    height: 141px;
    bottom: -50px;
    left: -50px;
    
}
@media (max-width:922px){
    .home__outermost{
        margin-top:20%;
        
    }
    .home__banner{
        height: 300px;
        width: 300px;
    }
    .home__profile{
        height: 400px;
    }
    .liner{
        display:none;
        
    }
    .home__container{
        margin-left: 0px;
        width: 100%;
        padding: 30px;
    }
    .home__data-two{
        left: -50px;

    }
    


}