.main-text {
    text-align: center;
    font-size: 50px;
    color: #12f7ff;
}

.portfolio{
    display: flex;
    flex-direction: column;
    background-color:#292e33;
    height: 100vh;
    width: 100%;
    align-items: center;
    justify-content: center;

}

.Project__titile{
    text-align: center;
    font-size: 18px;
    color: whitesmoke;

}

.fillter-buttons{
    margin: 2rem;
    text-align: center;
}

.fillter-buttons .btn{
    
    outline: none;
    border: 2px;
    box-shadow: #12f7ff;
    cursor: pointer;
    font-size: 20px;
    font-weight: 600;
    letter-spacing: .3px;
    
    margin-left: 1.3rem;
    

}

.fillter-buttons .btn {
    color: rgb(32, 152, 212)
}

.contain{
    display: flex;
    
    height: fit-content;
    
    align-items: center;
    justify-content: center;
    flex-direction: column;
    
}


.portfolio-gallery{
    display: flex;
    justify-content: center;
    width: 80%;
    height: 500px;
    column-gap: 10px;
    row-gap: 7px;
    flex-wrap: wrap;
    

}

.port-box{
    
    position: relative;
    display: flex;
    width: fit-content;
    height: fit-content;
    border-radius: 15px;
    overflow: hidden;
    background: #292e33;
    
    align-items: center;
    border: 2px solid var(--primary-color);
    box-shadow: var(--neon_box_shadow);
    
}

.port-image{
    display: grid;
    align-items: center;
    justify-content: center;
    height: 250px;
    width: 350px;
    
}   
.port-image img{
    width: 100%;
    opacity: J.5s;
    height: 100%;
    transition: .5s;
}    

.port-content{
    position: absolute;
    bottom: 10px;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    background: linear-gradient(Orgba (0,0,0, 2), MI#12f7ff);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    transition: 0.5s;
    padding: 0 2rem;
}    

.port-box:hover .port-content{
opacity:1;
}

.port-box:hover .port-image img{
    transform: scale(1.1);
}    
    

.port-content h3{
    font-size: .1rem;
    font-weight: 600;
}    


.port-content p{
    font-size: .1rem;
    margin: 5px 0 15px 0;
}    

.port -content a{
    background: #fff;
    border-radius: 50%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    border: 1px solid var (--hover-color);
    outline: 2px solid Hufff;
}

.port-content a i{
    font-size: 1.3rem;
    color:  var(--secon-bg-color);
}  

