.naivecontainer {
    display: flex;
    position: absolute;
    height: 68px;
    width: 100%;
    justify-content: flex-end;
    align-items: center;
    

}

.naivehead{
    margin-left: auto;
    position: relative;
    left: -90px;
    font-style: italic;
    height: 70px;
    align-items: center;


}



.Items {
    display: flex;
    margin-right: 10%;
    column-gap: 25px;
    width: fit-content;
    /*background-color: rgb(0, 149, 255);*/
    
}

.Item {

    color: aliceblue;
    font-style: italic;
    /*font-size: 21px;*/
    /*margin-left: 1%;*/
    font-weight: 1000;

    

    




  
    
}

.naivehead {
    display: flex;
    margin-right: auto;
    margin-left: 10%;
   
}
.headname {
    font-size: 30px;
    font-style: italic;
    font-weight: 1000;
    
    
}
.headname span{
    margin-left: 3px;
    color:var(--primary-color)
    
}

@media (max-width:922px){
    .Items{
        display: none;

    }
    .naivehead{
        left: 0;
        
    }
}