.project{
  display:flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.project_pill{
  display: flex;
  padding: 10px;
  width:30%;
  height: 70px;
  border-radius: 100px;
  background-color: var(--bg-color-alt);
  column-gap: 5px;
  align-items: center;
  justify-content: center;

}
.pill_content1,.pill_content2{
  position: relative;
  display: flex;
  width:100%;
  height:100%;
  
  font-size:25px ;
  font-weight: 600;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  
 
}
.pill_content1{
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
}

.pill_content2{
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
}
.background_pill_1,.background_pill_2{
  position: absolute;
  
  width: 100%;
  height: 100%;
  background-color: var(--primary-color);
  transform: translateX(-100%);
  transition: 0.5s ease-in-out;
  
  
}
.pill_content1 h1,.pill_content2 h1{
  z-index: 999;
}
.background_pill_1_active{
  transform: translateX(0);
}
.background_pill_2_active{
  transform: translateX(0);
}
.container {
  display: flex !important;

  flex-direction: column;
  margin-top: 80px;
  justify-content: space-between !important;
 
  width: 90%;
  
  
  
  

  
}

.heading {
  
  font-size: 2.5rem;
  
  text-align: center;
  margin-bottom: 20px;
}
.heading span{
  color: var(--primary-color);
}
.swiper_container {
  display: flex !important;
  flex-direction: column;
  width: 100% !important;
  
  height: 32rem;
  padding: 30px;
  align-items: center !important;
  
  
  
 
  
}

.swiper-slide {
  position: relative;
  bottom: -10px;
  display: flex!important;
  width: 35% !important;
  height: 23rem !important;
  border-radius: 2rem !important;
  justify-content: center;
  outline: 5px solid var(--primary-color);
  
  background-color: aquamarine;
  overflow:  hidden !important;
  transition: 0.2s ease-in-out !important;
  filter: blur(5px);
  pointer-events: none;
  
}
.Active{
  filter: none;
  pointer-events:all;
  box-shadow: 0 0 20px black;
  
  
}

.tech_title, .p{
  font-size: 100px;
  background-color: blue;
}

.project_1{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
 
  
}
.project_1_layer{
  display: flex;
  border-radius: 30px;
  padding: 20px;
  position: absolute;
  align-items: center;
  justify-content: center;
  
  width: 100%;
  height: 100%;
  background:linear-gradient(rgba(0,0,0,0.6),black) ;
  transform: translateY(100%);
  transition: 0.5s ease-in-out;
}
.project_1_layer h1 span{
  color: var(--primary-color);


}
.project_1_layer h1{
  margin-bottom: 5px;


}

.project_1:hover .project_1_layer{
  transform: translateY(0);
  

}
.swiper-slide:hover{
  box-shadow: 0 0 10px var(--primary-color);
  outline:  3px solid var(--primary-color) !important;
  

}



.swiper-slide img {
  width: 100% !important;
  height: 100%;
  border-radius: 2rem;
  object-fit: fill;
  
}

.swiper-slide-shadow-left,
.swiper-slide-shadow-right {
  display: none;
}

.slider-controler {
  display: flex !important;
  position: relative;
  flex-direction: column;
  width: 300px;
  height: 90px;
  bottom: 2rem;
  
  
  align-items: center;
  justify-content: center;
  
  
}





.slider-controler .slider-arrow {
  background: var(--primary-color);
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  
  filter: drop-shadow(0px 8px 24px rgba(18, 28, 53, 0.1));
}

.slider-controler .slider-arrow span{
  font-size: 3rem;
  font-weight: 900 !important;
  color: #222224;
}

.slider-controler .slider-arrow::after {
  content: '';
}

.swiper-pagination {
  
  
  width: 15rem !important;
  bottom: 1rem;
 
}

.swiper-pagination .swiper-pagination-bullet {
  position: relative;
  width:12px;
  height: 12px;
  top:-60px;
  right: -25px;
  filter: drop-shadow(0px 8px 24px rgba(18, 28, 53, 0.1));
}

.swiper-pagination .swiper-pagination-bullet-active {
  background: var(--primary-color);
}
@media(max-width:992px){
  .container{
    width: 100%;
  }
  .swiper-slide{
    width: 50% !important;
    background-color: red;
  }
  
}
.layer_content a{
  font-size: 20px;
  color: white;
  margin-top: 10px;
}
.layer_content a:hover{
  box-shadow: 0 0 10px var(--primary-color);

}
.btn1{
  display: flex;
  width: 100px;
  
  justify-content: center;
  align-items: center;
  padding:5px 14px;
  background: var(--primary-color);
  height: 35px;
  border-radius: 40px;
  box-shadow: 0 0 10px var(--shadow);
  font-size: 15px;
  color:#1f242d;
  
  text-decoration: none;
  transition: 0.4s ease;
  
}