.contact {
    background-image: var(--second-gradient);
}
.contact_section{
    display: flex;
    align-items: center;
    height: 100vh;
    justify-content: center;
    position: relative;
    flex-direction: column;
    
}
.contact_section h2{
    margin-top: 150px;

}
.contact_section h1{
    position: relative;
    top: -70px;
    margin-bottom: 10px;
}
.section__subtitles{
    color: var(--primary-color);
}
.section__subtitles span{
    color: whitesmoke;
    
}

.contact__container {
   
    display: flex;
    
    justify-content: space-evenly;
    align-items: center;
   
    width: 100%;

}
.contact__content{
    display: flex;
    width: fit-content;
   
    flex-direction: column;
    height: 350px;
    margin-bottom: 35px;

    justify-content: space-between;
}

.contact__card {
    display: flex;
    
    
    width: 300px;
    justify-content: flex-start;
    align-items: center;
    
    
}
.inner__card__content{
    display: block;
    margin-left: 60px;
}

.contact__card-icon,
.contact__form-input {
    border: 2px solid var(--border-color);
    background-color: var(--container-color);
}
.contact__form{
    display: block;
    width: 40%;

}

.contact__card-icon {
    
    position: relative;
    width: 45px;
    height: 45px;
    border-radius: 50%;
    box-shadow: var(--shadow);
    font-size: var(--larger-font-size);
    display: flex;
    align-items: center;
    justify-content: center;

}

.contact__card-icon::after {
    content:'';
    width: 40px;
    height: 2px;
    background-color: var(--border-color);
    position: absolute;
    left: 100%;
    top: 50%;
    margin-top: -1px;

}

.contact__card-title {
    font-size: var(--h5-font-size);
    font-family: var(--second-font);
    padding-block: 6px;
}

.contact__form-group {
    display: flex;
    
    width: 100%;
    column-gap: 30px;
    
   
    
    
    
}

.contact__form-div {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    margin-bottom: 20px;
    width: 100%;
    justify-content: flex-start;
    align-items: flex-start;
    
}

.contact__form-tag {
    margin-left: 20px;
   
    font-size: var(--smaller-font-size);
    font-weight: 500;

    
}
.contact__form-tag b {
    color: var(--primary-color);
}

.contact__form-input {
    
    color: var(--title-color);
    height: 40px;
    
    border-radius: 30px;
    padding-inline: 30px;
    width: 100%;
}

.contact__form-area .contact__form-input {
    height: 140px;
    
    
    padding-block: 20px;
   
}
.contact__form-input:focus{
    border: 2px solid var(--primary-color);
    box-shadow: 0 0 8px var(--primary-color);
}

.contact__submit {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    column-gap: 30px;
}